export default {
	data() {
		return {
			message:"进入商城",
			disabled:false,
			i:0,
			bg:`rgb(243,143,49)`
		}
	},
	methods: {
		clickgetUserInfo(){
			uni.getProvider({
				service:"oauth",
				success:(res)=>{//这里获取到是哪个平台
					// console.log(res)
					if (~res.provider.indexOf('weixin')){
						uni.login({
							provider:"weixin",
							success:(loginRes)=>{//这里获取到code码
								// console.log(JSON.stringify(loginRes))
								// console.log(loginRes)
								let appid = 'wx2fd0ff4416ce6219'
								let SECRET = 'e1d79c653010b1b3991b4b26edb9d143'
								uni.request({
									url:`https://api.weixin.qq.com/sns/jscode2session?appid=${appid}&secret=${SECRET}&js_code=${loginRes.code}&grant_type=authorization_code`,
									success:(wxres)=>{
										// console.log()//成功换取openid 和secet
										uni.request({
											url:"http://hbk.huiboke.com/api/login_and_register/userLogin",
											method:"POST",
											data:{
												login_type:'weixin',
												opened:wxres.data.openid
											},
											success:(bangres)=>{
												// console.log(bangres)
												// console.log(bangres.data.data.token)
												//这是存wxopenid
												uni.setStorage({
													key:"wxcodekey",
													data:wxres.data.openid
												})
												uni.setStorage({
													key:"bindtokey",
													data:bangres.data.data.token
												})
												// console.log(bangres)
												if(bangres.data.code==0){
													uni.setStorage({ //保存binddingcode状态 
														key:"binddingcode",
														data:bangres.data.code
													})
												}else{
													uni.setStorage({
														key:"binddingcode",
														data:bangres.data.code
													})
												}
												let i = 0;
												let [times,r,g,b] = [null,158,52,79]
												this.disabled = true
												times = setInterval(()=>{
													i++
													[r,g,b] = [r++,g++,b++]
													this.message = "正在拼命加载"
													// 57 181 74
													this.i = i+"%"
													this.bg = `rgb(${r},${g},${b})`
													if(i==100){
														clearInterval(times)
														this.disabled = false
														uni.switchTab({
															url:"/pages/index/index"
														})
													}
												},100)
											}
										})
									}
								})
								uni.getUserInfo({
									provider:"weixin",
									success:(infoRes)=>{
										// console.log(infoRes) //这获取了用户的信息
										//这里加点特效
										let i = 0;
										let [times,r,g,b] = [null,158,52,79]
										this.disabled = true
										times = setInterval(()=>{
											i++
											[r,g,b] = [r++,g++,b++]
											this.message = "正在拼命加载"
											// 57 181 74
											this.i = i+"%"
											this.bg = `rgb(${r},${g},${b})`
											if(i==100){
												clearInterval(times)
												this.disabled = false
												uni.switchTab({
													url:"/pages/index/index"
												})
											}
										},100)
									},
									fail(err){
										console.log(err)
									}
								})
							}
						})
					}
				}
			})
		}
	}
}
